<template>
  <div class="contact_page_wrapper" id="app">
    <section class="page_content">
      <div class="nav_menu">
        <a class="home_link" href="/">Home</a>
      </div>
      <div class="form_view" v-if="!isFormSubmitted">
        <h1 class="contact_title">Say hi</h1>
        <p class="form_info_basic">It's a form. You know what to do.</p>
        <p class="form_info_details">or email us at <a href="mailto:hello@epicmax.co" class="contact_email">hello@epicmax.co</a></p>
        <section class="form_wrapper">
          <form class="contact_form" @submit="checkForm">
            <div class="form_content">
              <MaterialInput
                type="text"
                name="username"
                v-model="username"
                label="Name"
                :isValid="validity.username"
                @validitySetter="inputValidSetter"
                errorText="Don't be shy."
              >What is your name?</MaterialInput>
              <MaterialInput
                type="text"
                name="email"
                v-model="email"
                label="Your fancy email"
                :isValid="validity.email"
                @validitySetter="inputValidSetter"
                errorText="That email address looks a bit weird 🤔"
              >Your fancy email</MaterialInput>
              <MaterialInput
                type="textarea"
                name="projectInfo"
                v-model="projectInfo"
                label="Your message"
                :isValid="validity.projectInfo"
                @validitySetter="inputValidSetter"
                errorText="Go on. Nothing bad will happen."
                isAttachable="true"
              >Tell us about your project</MaterialInput>
            </div>
            <div class="buttons_wrapper">
              <button type="submit" class="submit_wrapper">
                <div class="action_button">Send Message</div>
              </button>
            </div>
          </form>
        </section>
      </div>
      <div class="submitted_form_view" v-if="isFormSubmitted">
        <p class="contact_title">Thank you!</p>
        <p class="form_info_basic">Your message has been sent successfully.</p>
      </div>
    </section>
  </div>
</template>

<script>
import MaterialInput from "./components/MaterialInput";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      username: "",
      email: "",
      projectInfo: "",
      validity: {
        username: true,
        email: true,
        projectInfo: true
      },
      isFormSubmitted: false
    };
  },
  methods: {
    checkForm: function(e) {
      if (!this.username) {
        this.validity.username = false;
      }
      if (!this.email || !this.validEmail(this.email)) {
        this.validity.email = false;
      }
      if (!this.projectInfo) {
        this.validity.projectInfo = false;
      }
      if (
        this.validity.username &&
        this.validity.email &&
        this.validity.projectInfo
      ) {
		axios.post("https://formspree.io/hello@epicmax.co", {
			username: this.username,
			email: this.email,
			projectInfo: this.projectInfo
		}).then(() => {
			console.log('done');
			this.isFormSubmitted = true;
		});
      }
	  
	  e.preventDefault();
    },
    validEmail: function(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    inputValidSetter: function(field, newVal) {
      this.validity[field] = newVal;
    }
  },
  components: {
    MaterialInput
  }
};
</script>

