<template>
  <div class="material_input_component" :class="computedClasses">
    <div class="input_wrapper">
      <input
        v-if="type === 'text'"
        type="text"
        class="material_input"
        :name="name"
        :id="id"
        :placeholder="placeholder"
        v-model="inputValue"
        @focus="handleFocus(true)"
        @blur="handleFocus(false)"
        @input="handleModelInput"
      >
      
      <textarea
        v-if="type === 'textarea'"
        type="text"
        class="material_input"
        rows="1"
        v-autosize
        :name="name"
        :id="id"
        :placeholder="placeholder"
        v-model="inputValue"
        @focus="handleFocus(true)"
        @blur="handleFocus(false)"
        @input="handleModelInput"
      ></textarea>
    </div>
    <div
      class="attachment_container"
      v-if="(isAttachable && focus) || (isAttachable && files.length) || (isAttachable && this.inputValue)"
    >
      <div class="attachment_file_input">
        <label class="attachment_file_input_label" @mousedown="keepFocus()" for="inputfile">
          <span class="file_name">{{ attachmentFileName }}</span>
          <input
            id="inputfile"
            type="file"
            @change="fileChangeHandler"
            name="attachment"
            class="attachment_file_input_field"
          >
        </label>
      </div>
    </div>
    <span class="material_input_bar"></span>
    
    <label class="material_placeholder">
      <slot></slot>
    </label>
    <label class="material_label">{{ label }}</label>
    <div class="material_error" :class="{'visibility_none': isValid || focus }">{{ errorText }}</div>
  </div>
</template>

<script>
import autosize from "v-autosize";

export default {
  name: "material_input",
  directives: {
    autosize
  },
  computed: {
    computedClasses() {
      return {
        material_active: this.focus,
        material_has_errors: Boolean(!this.isValid && !this.focus),
        material_raised: Boolean(
          this.focus ||
            this.inputValue ||
            (this.placeholder && !this.inputValue) ||
            this.files.length
        ),
        attachable: this.isAttachable,
        has_content: this.files.length || this.inputValue
      };
    },
    attachmentFileName() {
      if (this.files.length > 0 && "name" in this.files[0]) {
        return this.files[0].name;
      }
      return this.attachmentFilePlaceholder;
    }
  },
  data() {
    return {
      inputValue: null,
      focus: false,
      isFocusUpdateNeed: true,
      valid: true,
      files: []
    };
  },
  methods: {
    handleModelInput(event) {
      this.$emit("input", event.target.value, event);
      this.handleValidation();
    },
    handleFocus(focused) {
      if (this.isFocusUpdateNeed) {
        this.focus = focused;
      } else {
        this.isFocusUpdateNeed = true;
      }
      this.$emit("validitySetter", this.name, true);
    },
    handleValidation() {
      this.valid = this.$el
        ? this.$el.querySelector(".material_input").validity.valid
        : this.valid;
    },
    fileChangeHandler(e) {
      this.files = Array.from(e.target.files);
    },
    keepFocus() {
      this.isFocusUpdateNeed = false;
      document.body.onfocus = () => {
        document.body.onfocus = null;
        this.$el.querySelector(".material_input").focus();
      };
    }
  },
  props: {
    id: {
      type: String,
      default: null
    },
    name: {
      type: String,
      default: null
    },
    type: {
      type: String,
      default: "text"
    },
    placeholder: {
      type: String,
      default: null
    },
    label: {
      type: String,
      default: null
    },
    attachmentFilePlaceholder: {
      type: String,
      default: "Add attachment"
    },
    isValid: {
      type: Boolean
    },
    errorText: {
      type: String
    },
    isAttachable: {
      type: String,
      default: null
    }
  }
};
</script>

