  position: relative;
  display: block;
  width: 100%;
}

.material_input_component.material_raised .material_placeholder {
  opacity: 0;
  top: -20px;
  font-size: 1rem;
  line-height: 1.25em;
}
.material_input_component.material_raised .material_label {
  opacity: 1;
